const overlay = document.querySelector('.js-overlay');
const menu = document.querySelector('.js-header-menu');
const button = document.querySelector('.js-menu-toggle');
const header = document.querySelector('.js-header');

const closeMenu = () => {
  button.classList.remove('header__toggle--active');
  menu.classList.remove('header__list--active');
  overlay.classList.remove('header__overlay--active');
  header.classList.remove('header--active');
  document.body.style.overflow = 'inherit';
};

const openMenu = () => {
  button.classList.add('header__toggle--active');
  menu.classList.add('header__list--active');
  overlay.classList.add('header__overlay--active');
  header.classList.add('header--active');
  document.body.style.overflow = 'hidden';
};


const mobileMenu = () => {
  const menuFun = () => {
    if (button.classList.contains('header__toggle--active')) {
      closeMenu();
      overlay.removeEventListener('click', closeMenu);
    } else {
      openMenu();
      overlay.addEventListener('click', closeMenu);
    }
  };

  if (button) {
    button.addEventListener('click', menuFun);
  }
};

export {mobileMenu, closeMenu};
