// https://flatpickr.js.org/

const calendar = () => {
  flatpickr('.anotherSelector', {
    locale: 'ru',
    dateFormat: 'd.m.Y',
    minDate: 'today',
    maxDate: '30.09.2021',
    enable: [
      '3.06.2021',
      '5.06.2021',
      '7.06.2021',
      '12.06.2021',
      '15.06.2021',
      '19.06.2021',
      '20.06.2021',
      '21.06.2021'],
    disableMobile: true,
  });
};

export {calendar};
