const selectsTime = document.querySelectorAll('[data-select-time]');

const activeElement = (list, field) => {
  list.addEventListener('click', (evt) => {
    const label = evt.target.closest('[data-time]');

    if (!label) {
      return;
    }

    field.value = label.dataset.time;
    list.classList.remove('buy-tickets__time-list--active');
  });
};

const openMenu = (evt) => {
  const field = evt.target.closest('[data-select-time]');

  if (!field) {
    return;
  }

  const list = field.parentElement.querySelector('.js-time-list');

  if (list) {
    list.classList.toggle('buy-tickets__time-list--active');
    activeElement(list, field);
  }
};

const selectTime = () => {
  selectsTime.forEach((select) => {
    select.addEventListener('click', openMenu);
  });
};

export {selectTime};
