import {iosVhFix} from './utils/ios-vh-fix';

import {initModals} from './modules/modals/init-modals';
import {scrollHeader} from './modules/scroll-header';
import {departureBerthsSwiper} from './modules/departure-berths';
import {mobileMenu} from './modules/header-menu';
import {scrollToElement} from './modules/scroll-to-element';
import {calendar} from './modules/calendar';
import {selectTime} from './modules/select-time';
import {hiddenStickyPricePopup} from './modules/hidden-sticky-price-popup';
import {sliderAbout} from './modules/slider-about';
import {departureBerthsTabs} from './modules/departure-berths-tabs';
import {departureBerthsMap} from './modules/yandex-map';
import {initFormValidate} from './modules/form-validate/init-form-validate';
import {selectCustom} from './modules/custom-select/custom-select';

// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  // только если ie11
  // ieFix();
  // https://github.com/fregante/object-fit-images
  // polyfillObjectFit();

  iosVhFix();

  // Modules
  // ---------------------------------
  scrollHeader();
  mobileMenu();
  scrollToElement();

  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {
    initModals();
    initFormValidate();
    departureBerthsSwiper();
    calendar();
    selectTime();
    hiddenStickyPricePopup();
    sliderAbout();
    departureBerthsTabs();
    departureBerthsMap();
    selectCustom.init();
  });
});

