import {departureBerthsSwiper} from './departure-berths';

const tabsContainers = document.querySelectorAll('.js-tabs-container');
const tabsListBtn = document.querySelectorAll('[data-tabs]');
const tabsListItem = document.querySelectorAll('[data-tabs-item]');

const setButtonActive = (settingValue) => {
  const activeButton = Array.from(tabsListBtn)
      .find((element) => element.classList.contains('active'));

  activeButton.classList.remove('active');

  const setButton = Array.from(tabsListBtn)
      .find((element) => element.dataset.tabs === settingValue);

  setButton.classList.add('active');
};

const setTabActive = (value) => {
  const activeTab = Array.from(tabsListItem)
      .find((element) => element.classList.contains('active'));

  activeTab.classList.remove('active');

  const setTab = Array.from(tabsListItem)
      .find((element) => element.dataset.tabsItem === value);

  setTab.classList.add('active');
};

const settingButtonClickHandler = (evt) => {
  const button = evt.target.closest('button');

  if (!button) {
    return;
  }

  const settingValue = button.dataset.tabs;
  setButtonActive(settingValue);

  setTabActive(settingValue);
  departureBerthsSwiper();
};


const init = () => {
  tabsContainers.forEach((container) => {
    container.addEventListener('click', settingButtonClickHandler);
  });
};

const departureBerthsTabs = () => {
  init();
};

export {departureBerthsTabs};
