const departureBerthsSwiper = () => {
  const containers = document.querySelectorAll('.js-slider-departure-berths');
  const breakpoint = window.matchMedia('(max-width:768px)');

  if (containers) {
    containers.forEach((container) => {
      let mySwiper;
      const breakpointChecker = () => {
        if (breakpoint.matches && container) {
          mySwiper = new Swiper (container, {
            freeMode: true,
            slidesPerView: 'auto',
            spaceBetween: 16,
          });
        } else {
          if (mySwiper) {
            mySwiper.destroy();
          }
        }
      };
      breakpoint.addListener(breakpointChecker);
      breakpointChecker();
    });
  }
};

export {departureBerthsSwiper};
