const stickyPrice = document.querySelector('.js-sticky-price');

const hiddenStickyPricePopup = () => {
  const element = stickyPrice.querySelector('a[href^="#"]').getAttribute('href');
  const containerName = stickyPrice.querySelector('a[href^="#"]').dataset.scrollContainer;
  const container = document.querySelector(containerName);
  const elementScroll = document.querySelector(element);

  const breakPointSticky = () => {
    container.addEventListener('scroll', function () {
      if (container.scrollTop < elementScroll.offsetTop - elementScroll.scrollHeight) {
        stickyPrice.style.display = 'flex';
      } else {
        stickyPrice.style.display = 'none';
      }
    });
  };

  const breakpoint = window.matchMedia('(max-width:1022px)');
  const breakpointChecker = () => {
    if (breakpoint.matches) {
      breakPointSticky();
      stickyPrice.style.display = 'flex';
    } else {
      stickyPrice.style.display = 'none';
    }
  };

  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
};

export {hiddenStickyPricePopup};
