const sliderAbout = () => {
  const container = document.querySelector('.slider-sights--about');

  const breakpoint = window.matchMedia('(max-width:2400px)');
  let mySwiper;
  const breakpointChecker = () => {
    if (breakpoint.matches && container) {
      mySwiper = new Swiper (container, {
        slidesPerView: 'auto',
        spaceBetween: 16,
        // centeredSlides: true,
        navigation: {
          nextEl: '.slider-sights__btn--next',
          prevEl: '.slider-sights__btn--prev',
        },
      });
    } else {
      if (mySwiper) {
        mySwiper.destroy();
      }
    }
  };
  breakpoint.addListener(breakpointChecker);
  breakpointChecker();
};

export {sliderAbout};
