const departureBerthsMap = () => {
  let myMapSpb;
  let myMapKronstadt;

  const init = () => {
    myMapSpb = new ymaps.Map('mapSpb', {
      center: [59.940666, 30.308233],
      zoom: 14,
      controls: ['zoomControl'],
      behaviors: ['drag'],
    });

    myMapKronstadt = new ymaps.Map('mapKronstadt', {
      center: [59.987763, 29.752994],
      zoom: 13,
      controls: ['zoomControl'],
      behaviors: ['drag'],
    });

    const optionsPin = {
      iconLayout: 'default#image',
      iconImageHref: 'img/pin.svg',
      iconImageSize: [54, 67],
      iconImageOffset: [-27, -46],
    };

    let myPlacemarkSpb1 = new ymaps.Placemark([59.942031, 30.314136], {}, optionsPin);
    let myPlacemarkSpb2 = new ymaps.Placemark([59.937420, 30.302166], {}, optionsPin);

    let myPlacemarkKronstadt1 = new ymaps.Placemark([59.989516, 29.736499], {}, optionsPin);
    let myPlacemarkKronstadt2 = new ymaps.Placemark([59.985013, 29.770769], {}, optionsPin);


    myMapSpb.geoObjects
        .add(myPlacemarkSpb1)
        .add(myPlacemarkSpb2);

    myMapKronstadt.geoObjects
        .add(myPlacemarkKronstadt1)
        .add(myPlacemarkKronstadt2);
  };

  ymaps.ready(init);
};

export {departureBerthsMap};

